<template>
    <div>
        <Strong style="font-size: 24px">管理员登陆</Strong>
        <Divider />
        <div style="width: 20%;margin: auto">
            <Input type="text" v-model="formInline.username" placeholder="Username">
                <Icon type="ios-person-outline" slot="prepend"></Icon>
            </Input>
        </div>
        <br>
        <div style="width: 20%;margin: auto;">
            <Input type="password" v-model="formInline.password" placeholder="Password">
                <Icon type="ios-lock-outline" slot="prepend"></Icon>
            </Input>
        </div>
        <br>
        <div>
            <Button type="primary" @click="login">Signin</Button>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    export default {

        name: "managerlogin",
        data () {
            return {

                formInline: {
                    username: '',
                    password: ''
                },
            }
        },
        methods:{
            login:function () {
                console.log(this.formInline.username);
                console.log(this.formInline.password);
                axios({
                    url:'http://localhost:8976/aduser/login',
                    method:'get',
                    params: {
                        username:this.formInline.username,
                        password:this.formInline.password,
                    }
                }).then((res)=>{
                    console.log(res);
                    if(res.data!="username or password wrong"){
                        this.$Message.success('登录成功');
                        this.$router.push({  //核心语句
                            path:'/usermanage',   //跳转的路径
                            query:{           //路由传参时push和query搭配使用 ，作用时传递参数
                                id:this.id ,
                            }
                        })
                    }else{
                        this.$Message.error('用户名或密码错误，请重新登陆');
                    }
                })
            }
        }
    }
</script>

